* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul li {
  list-style: none;
  text-decoration: none;
}
ul li a {
  text-decoration: none;
}
body {
  background: rgb(246, 239, 239);
}
/* >>>>>>>>>-navbar-start */
ul {
  display: flex;
  gap: 35px;
}
.search__icon {
  padding-top: 20px;
}
.buy__icon {
  padding-top: 20px;
}
.button {
  margin-top: 12px;
  width: 120px;
  height: 40px;
  background: #ff7a50;
  box-shadow: 0px 22px 40px rgba(255, 104, 56, 0.19);
  border-radius: 5px;
  border: none;
  color: #fff;
}
/* >>>>>>>>>-navbar-end */

/* >>>>>>>>>>>>-header-start */
.headerr {
  display: flex;
  justify-content: space-evenly;
}
.header__left {
  padding-top: 140px;
}
.header__left__h1 {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 500;
  font-size: 66px;
  line-height: 78px;
  text-transform: capitalize;
  color: #363853;
}
.header__left__h1 span {
  color: #ff7a50;
}
.header__left__p {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  text-transform: capitalize;
  color: #363853;
}
.btns {
  display: flex;
  gap: 30px;
}
.btn__1 {
  width: 160px;
  height: 50px;
  border: none;
  color: #fff;
  background: #ff7a50;
  box-shadow: 0px 22px 40px rgba(255, 104, 56, 0.19);
  border-radius: 5px;
}
.btn__2 {
  border: none;
  background: none;
}
.header__right img {
  width: 600px;
  padding-top: 40px;
}
/* >>>>>>>>>>>>-header-end */
/* cardd-start */

/* card-end */
.card__btns {
  display: flex;
  justify-content: space-evenly;
  margin-top: 60px;
}
.cardd h1 {
  text-align: center;
  margin-top: 40px;
  font-family: "Rubik";
  font-style: normal;
  font-weight: 500;
  font-size: 39px;
  line-height: 43px;
  text-transform: capitalize;
  color: #363853;
}
.cardd h1 span {
  color: #ff7a50;
}
.btn img {
  width: 30px;
}
.btn {
  display: flex;
  padding: 23px;
  border-radius: 10px;
  background: #ffffff;
  gap: 10px;
  align-items: center;
  transition: 0.6s;
}
.btn:hover {
  border: none;
  border-radius: 10px;
  box-shadow: 0 0 20px 0px grey;
  color: #fff;
  background: #ff7a50;
}
.card__images {
  margin-top: 50px;
  display: flex;
  justify-content: space-evenly;
}
/* card__1 satart */
.card__1 {
  display: flex;
  gap: 35px;
  margin-top: 90px;
}
.card__1__right h1 {
  margin-top: 50px;
  font-family: "Rubik";
  font-style: normal;
  font-weight: 500;
  font-size: 46px;
  line-height: 50px;
  text-transform: capitalize;
  color: #ff7a50;
}
.card__1__right h1 span {
  color: #363853;
}
.card__1__right p {
  padding-top: 50px;
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-size: 25px;
  line-height: 30px;
  color: #363853;
  opacity: 0.9;
}
.card__1__right h3 {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 30px;
  text-transform: capitalize;
  color: #363853;
}
.card__1__right button {
  width: 200px;
  height: 50px;
  border: none;
  color: #fff;
  margin-top: 40px;
  background: #ff7a50;
  box-shadow: 0px 22px 40px rgba(255, 104, 56, 0.19);
  border-radius: 30px 30px 1px 30px;
}
/* caerd__1 end */
/* card__2-start */
.card__2__h1 {
  margin-top: 30px;
  text-align: center;
  font-family: "Rubik";
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 30px;
  text-transform: capitalize;
  color: #363853;
}
.card__2 h1 span {
  color: #ff7a50;
}
.carts {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 60px;
  justify-content: center;
  margin-top: 50px;
}
.carts__carta {
  width: 361px;
  height: 417px;
  left: 135px;
  top: 2563px;
  background: #ffffff;
  border-radius: 53px 5px;
}
.carts__carta img{
  width: 100%;
}
.h1__btn {
  display: flex;
  gap: 10px;
}
.carts__carta h5 {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 30px;
  margin-left: 55px;
  padding-top: 20px;
  color: #363853;
}
.h1__btn {
  display: flex;
  justify-content: space-evenly;
}
.h11 {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 30px;
  color: #363853;
  margin-left: 30px;
  opacity: 0.9;
}
.btnn {
  width: 90px;
  border: none;
  color: #fff;
  background: #ff7a50;
  box-shadow: 0px 5px 12px rgba(255, 104, 56, 0.19);
  border-radius: 20px 20px 0px 20px;
}

/* card__2-end */
/* card__3-start */
.card__3 {
  display: flex;
  justify-content: space-evenly;
  margin-top: 70px;
}
.card__3__left {
  padding-top: 50px;
}
.card__3__left h1 {
  padding-top: 30px;
  font-family: "Rubik";
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 50px;
  text-transform: capitalize;
  color: #363853;
}
.card__3__left h1 span {
  color: #ff7a50;
}
.card__3 p {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: #363853;
  opacity: 0.9;
}
/* card__3-end */
/* card__4-start */
.card__4 {
  margin-top: 50px;
}
.carddddd {
  display: flex;
  justify-content: space-evenly;
}
.card__4 h1 {
  text-align: center;
  font-family: "Rubik";
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 43px;
  letter-spacing: 0.5px;
  color: #363853;
}
.card__4 h1 span {
  color: #ff7a50;
}
.card__4__left {
  margin-top: 50px;
}
.card__4__right p {
  margin-top: 170px;
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 45px;
  letter-spacing: 0.5px;
  color: #363853;
}
/* card__4-end */

/* start footer */
footer{
  margin-top: 50px;
  background-color: #f5a086;
}
.footer_1 {
  padding-top: 30px;
  display: flex;
  justify-content: space-between;
  gap: 50px;

}
.part1 ul {
  flex-direction: column;
